	module.exports = {"data":{"markdownRemark":{"html":"<p>访L家。他说在B市打一场官司，情况是他经一位北京朋友介绍，认识那边一女子。那女人说是市司法局的，和他谈2020台大哥大的生意，价值1400多万元。他不放心，还去过那里，见了司法局长和市长。这样就发了货。但是那边一直不付款。结果司法局也不承认，说这个女人是他们聘用的，不是正式职工。这件事情花了很长的时间没有处理好。这次由上海公安局出面，发出\"协查通知\"。结果在锦州抓住该女。公安局已经通知他了。不知最后结果会是什么。这就是中国经济发展中出现的新问题，也是一种无秩序现象的反映。问题是司法局作为国家机关，怎么能卷入这样的事情?</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0122.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0122.md","frontmatter":{"title":"7月17日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0122.md absPath of file >>> MarkdownRemark"}}

